<template>
  <div>
    <el-form
      ref="form"
      :model="formData"
      label-width="80px"
      style="width:400px;margin:auto;margin-top:50px"
    >
      <el-form-item label="">
        <el-upload
          class="upload-demo"
          name="files"
          ref="upload"
          action="http://localhost:8080/upload/singlefile"
          :on-preview="handlePreview"
          :on-remove="handleRemove"
          :on-success="handleSuccess"
          :file-list="fileList"
          :auto-upload="true">
          <el-button slot="trigger" size="small" type="primary"></el-button>
          <!--<el-button style="margin-left: 10px;" size="small" type="success" @click="submit"></el-button>-->
          <div slot="tip" class="el-upload__tip">jpg/png500kb</div>
        </el-upload>
      </el-form-item>
      <el-form-item label="ID">
        <el-input v-model="formData.aId" placeholder="ID"></el-input>
      </el-form-item>
      <el-form-item label="">
        <el-input v-model="formData.company" placeholder=""></el-input>
      </el-form-item>
      <el-form-item label="">
        <el-select v-model="formData.path" placeholder="" style="width:100%">
          <el-option
            v-for="(item,index) in urls"
            :key="index"
            :label="item.label"
            :value="item.path"
          ></el-option>
        </el-select>
      </el-form-item>
      <!--<el-form-item label="">-->
        <!--<el-input v-model="formData.params" placeholder=""></el-input>-->
      <!--</el-form-item>-->
      <el-form-item label="">
        <el-input v-model="formData.remark" placeholder=""></el-input>
      </el-form-item>
      <el-form-item label="">
        <el-input v-model="formData.sort" placeholder=""></el-input>
      </el-form-item>
      <el-form-item label="">
        <el-switch v-model="isShow"></el-switch>
      </el-form-item>
      <el-form-item>
        <el-button type="primary" @click="onSubmit" :loading="loading"></el-button>
      </el-form-item>
    </el-form>
    <el-dialog :visible.sync="centerDialogVisible" width="1100px" center>
      <gallery :clear="clear"></gallery>
      <span slot="footer" class="dialog-footer">
        <el-button @click="centerDialogVisible = false"> </el-button>
        <el-button type="primary" @click="chooseIt()"> </el-button>
      </span>
    </el-dialog>
  </div>
</template>
<script>
import gallery from "../file/gallery.vue";
let app = require("./calousels_edit.js");
app.components = { gallery };
export default app;
</script>



